.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  /* display: flex;
  flex-direction: column; */
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

div#main-content {
  margin: 0px 70px;
  padding: 10px 10px;
  font-size: 20px;

  position: fixed;
  transition: 350ms;
  -webkit-transition: 0.35s;
  left: 0px;
} div#main-content.moved {
  left: 150px;
}



a.normlink {
  text-decoration: none;
  display: inline-block;
  padding: 8px 16px;
} a.normlink:hover {
  background-color: #ddd;
  color: black;
}


.toggle-btn {
  position: absolute;
  padding: 8px 8px;
  left: -70px;
  background: #e1e1e1;
  top: 10px;
  cursor: pointer;
} .toggle-btn span {
  width: 30px;
  height: 2px;
  background: #444;
  display: block;
  margin-bottom: 6px
} .toggle-btn span:last-child {
  margin-bottom: 0;
}

ul.navbar-nav {
  list-style: none;
  margin-top: 10px;
}

.navbar-nav li {
  -webkit-user-select: none; /* Safari */ -moz-user-select: none; /* Firefox */ -ms-user-select: none; /* IE10+/Edge */ user-select: none; /* Standard */
  list-style-type: none;
  width: 100%;
  height: 40px;
  padding: 10px 15px;
  box-sizing: border-box;
  border-bottom: 1px solid gray;
  font-size: 20px;
} .navbar-nav li:hover {
  background: #3a3acce0;
  font-size: 22px;
} .navbar-nav a {
  color: #e1e1e1;
  text-decoration: none;
} 




